.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  justify-content: center;
}

.modal-field {
  width: 100%;
  height: 70%;
  top: 0;
  background: rgba(0, 0, 0, 0);
}

.modal-body {
  width: 100%;
  height: null;
  flex: 1;
  position: fixed;
  padding-top: 15px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  justify-content: center;
  background: #fff;
  bottom: 0;
}

.deleteBtns {
  display: flex;
  justify-content: center;
  margin-left: 1rem;
  margin-right: 1rem;
  align-items: center;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
}
